@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-20px);
  }
  60% {
    transform: translateY(-10px);
  }
}

.loading-text {
  color: #bf132d;
  font-size: 18px;
  animation: bounce 2s infinite; /* Apply bounce animation */
}
